@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bai+Jamjuree:wght@100;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Syncopate:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bodoni+Moda:wght@100;700&display=swap);
* {
  padding: 0;
  margin: 0;
}

html,
body {
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.section-header{text-transform:uppercase;font-weight:600;letter-spacing:1px;font-family:"Syncopate", sans-serif;color:#626262}

body{font-family:"Poppins", sans-serif;background-color:#f5f0ec}.main-container{padding:0 5vw}section{padding:100px 0}section .section-header{margin-bottom:50px}

.about-section p{font-size:70px;line-height:1.12;opacity:0}.about-section p>div{opacity:0;-webkit-transform:translateY(0px);transform:translateY(0px)}.about-section p.is-reveal{opacity:1}.featured-section{display:grid;grid-template-columns:1fr 3fr;-webkit-align-items:center;align-items:center}.featured-section img{-webkit-clip-path:inset(100% 0% 0% 0%);clip-path:inset(100% 0% 0% 0%);transition:-webkit-clip-path 1s cubic-bezier(0.77, 0, 0.175, 1);transition:clip-path 1s cubic-bezier(0.77, 0, 0.175, 1);transition:clip-path 1s cubic-bezier(0.77, 0, 0.175, 1), -webkit-clip-path 1s cubic-bezier(0.77, 0, 0.175, 1)}.featured-section img.is-reveal{-webkit-clip-path:inset(0% 0% 0% 0%);clip-path:inset(0% 0% 0% 0%)}.featured-section h6{font-size:22px;text-transform:uppercase;font-weight:500}.featured-section .featured-row-layout,.featured-section .featured-column-layout{display:grid;grid-gap:10px;gap:10px}.featured-section .featured-row-layout{grid-template-rows:repeat(2, auto)}.featured-section .featured-row-layout img{width:100%;object-fit:cover}.featured-section .featured-column-layout{grid-template-columns:100px auto;-webkit-align-items:flex-end;align-items:flex-end}.featured-section .featured-column-layout img{width:100%;height:125vh;object-fit:cover}.featured-section .featured-column-layout h6{-webkit-transform:translateX(100%) rotate(-90deg);transform:translateX(100%) rotate(-90deg);-webkit-transform-origin:left bottom;transform-origin:left bottom;justify-self:self-end}.footer{text-align:center;padding-bottom:200px}.footer .location{font-size:18vw;text-transform:uppercase;font-family:"Bodoni Moda", serif;opacity:0}.footer .location.is-reveal{opacity:1}.gallery-wrap{background-color:#1f1f1f;margin-left:-5vw;margin-right:-5vw;position:relative}.gallery-counter{position:absolute;top:10%;left:100px;z-index:1;mix-blend-mode:difference;line-height:16px;color:#dbd8d6;font-family:"Bai Jamjuree";font-weight:600;font-size:16px;-webkit-font-smoothing:antialiased;display:inline-block}.gallery-counter .divider{content:"";background-color:white;width:6.25vw;margin:7px 10px;height:1px;display:inline-block}.gallery{height:80vh;padding:10vh 0;width:400%;display:-webkit-flex;display:flex;-webkit-flex-wrap:nowrap;flex-wrap:nowrap}.gallery .gallery-item{width:100%;height:100%;position:relative;will-change:transform}.gallery .gallery-item-info{position:absolute;bottom:10%;z-index:1;-webkit-transform:translateX(-20%);transform:translateX(-20%);color:#dbd8d6;color:white}.gallery .gallery-item-info .gallery-info-title{line-height:6vw;font-family:"Bai Jamjuree";font-weight:600;font-size:6vw;-webkit-font-smoothing:antialiased}.gallery .gallery-item-info .gallery-info-subtitle{position:relative;line-height:6vw;color:transparent;font-family:"Bodoni Moda";font-weight:400;font-size:6vw;-webkit-font-smoothing:antialiased;-webkit-text-stroke:2px #dbd8d6}.gallery .gallery-item-info .gallery-info-category{position:relative;line-height:24px;font-family:"Bai Jamjuree";font-weight:400;font-size:24px;-webkit-font-smoothing:antialiased;margin-top:2vh}.gallery .gallery-item-wrapper{aspect-ratio:16/9;height:100%;display:grid;grid-template-columns:20vw 1fr 200px;width:100vw}.gallery .gallery-item-wrapper.is-reveal .gallery-item-image{-webkit-transform:scale(1);transform:scale(1);-webkit-filter:none;filter:none}.gallery .gallery-item-image{background-size:cover;background-position:center;-webkit-transform-origin:center;transform-origin:center;width:100%;height:100%;will-change:transform;-webkit-transform:scale(0.7);transform:scale(0.7);transition:all 1.5s cubic-bezier(0.77, 0, 0.175, 1);-webkit-filter:grayscale(100%) sepia(20%) brightness(80%);filter:grayscale(100%) sepia(20%) brightness(80%)}.header-container{position:relative}.header-container .header-menu{position:absolute;left:0;top:100px;letter-spacing:1px;font-family:"Syncopate", sans-serif;color:#626262;font-weight:600;text-transform:uppercase}.header-container .header-menu li{margin:10px 0}#header-text{font-size:16vw;text-transform:uppercase;text-align:center;font-family:"Bai Jamjuree", sans-serif;font-weight:600}.lineParent{overflow:hidden}.lineChildren{-webkit-transform:translate(0, 500px);transform:translate(0, 500px)}.navbar{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;padding:50px 0;font-size:1.5rem;letter-spacing:1px;font-family:"Syncopate", sans-serif;color:#464646;font-weight:700;text-transform:uppercase}.loader-wrapper{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;-webkit-flex-direction:column;flex-direction:column;transition:"all 0.25s ease-in-out";background-color:#191919;color:#dbd8d6}.loader-wrapper h1{font-size:1.5vw;text-transform:uppercase;font-family:"Bai Jamjuree", sans-serif;font-weight:600}.loader-wrapper h2{font-size:1.5vw;text-transform:uppercase;font-family:"Bodoni Moda", serif;font-style:italic;margin-top:10px}.absolute{position:fixed;top:0;left:0;bottom:0;right:0;background-color:black;z-index:1}

